.dialogs {
    font-size: 30px;
    display: grid;
    grid-template-columns: 2fr 10fr;
}

.dialogsItems {
    padding: 10px;
}
.dialogsItems .active {
    color: gold;
}

.message {
    padding: 10px;
}

.dialogs img {
    width: 40px;
    height: 40px;
    border-radius: 15px;
}
